.nav__list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  display: block;
  text-decoration: none;
  color: $maincolor;
  padding: 23px 0;
  font-size: 12px;
  font-weight: 500;
  text-transform: uppercase;

  @include aimHover;
}

.nav__link:hover {
  color: $blue-accent;
}

.is-active {
  color: $blue-accent;
  position: relative;
}

.is-active::after {
  content: "";
  position: absolute;
  background-color: $blue-accent;
  left: 0;
  bottom: 0;
  height: 4px;
  width: 100%;
  border-radius: 8px;
}
